@import "mixin_media";

html {
  height: 100%;
  overflow-x: hidden;
}


body {
  display: flex;
  flex-direction: column;
  height: 100%;
  margin: 0;
  font-family: 'Roboto', sans-serif;
  /*скрыть дерганье страницы в появлении полосы прокрутки*/
  padding: 0 calc(14px - (100vw - 100%)) 0 0;
  //width: 100%;
  @include _400{
    padding: 0 ;
  }

}

