@import "../mixin_media.scss";
@import '../normalize.scss';

@import '../components/footer/footer.scss';
@import '../components/forms.scss';


main {
  display: flex;
  flex: 1 0 auto;
  max-width: 1310px;
  margin: auto;
  padding: 0 10px;
  flex-flow: column;
}

.card {
  width: 50%;
  margin: 65px auto;

  h1, h2, p{
    color: #4e4e4e;
  }

  
  p {
    font-size: 1rem
  }
  @include _300 {
    & > h1,  & > h2{
      font-size: 12px;
    }
    & > p {
      font-size: 10px;
    }
  }

  @include _600 {
    width: 95%;
    margin: 30px auto;
  }



}

